import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ModalController } from '@ionic/angular';
import { ApicallService } from 'src/app/services/CallApi/apicall.service';
import { GlobalService } from 'src/app/services/data/global.service';
import { ModalPage } from '../modal/modal.page';
import { Share } from '@capacitor/share';

@Component({
  selector: 'app-all-instalments',
  templateUrl: './all-instalments.page.html',
  styleUrls: ['./all-instalments.page.scss'],
})
export class AllInstalmentsPage implements OnInit {

  show = false;
  filterTerm!:string
  // public Category: any = 'Customer' ;
  public Category: any = 'monthlyinstallmentDetails' ;
  See_Customer: any;
  public by_Month: any={i_month:''}
  student_Installments: any;
  public selectTabs:any = 'Luqman Town';

  public installment_by_id:any;

  sharingText = 'Please Pay Your Monthly Plot Installment';
  whatsappSubject = 'Installment';
  sharingImage = [''];


  constructor(public apiCall:ApicallService, public modal : ModalController , private route: Router , public global: GlobalService, public modalController: ModalController) { }

  ngOnInit() {
  }

  refresh(){
    this.show= true;
    setTimeout(() => {
      this.show = false;
      this.apiCall.GetInstallmentsbymonth(this.by_Month.i_month);
    }, 1500 ); 
   }



   async select_customer_Data($event){
    console.log($event.target.value);
    this.by_Month.i_month = $event.target.value;
    console.log(this.by_Month);
    await this.apiCall.GetInstallmentsbymonth(this.by_Month.i_month);
    await this.global.Monthlyinstallment.subscribe(res=>{
      this.See_Customer = res;
      console.log(res);
    }); 
  }



   async sharesocial(data){
    console.log(data);
    const x = JSON.stringify(data)
    await Share.share({
      title: 'Monthly Installment',
      url: x,
      dialogTitle: 'Please Pay Your Monthly Plot Installment',
      text: 'Zagham'
    });
  }


  // Delete function of customer
  async Delete_Customer(customer_id) {
    const x = {customer_id:customer_id}
    await this.apiCall.api_del_customer(x);
    this.apiCall.GetInstallmentsbymonth(this.by_Month.i_month);
    console.log(customer_id);
  }

  // async view_all_installment(customer_id) {
  //   await this.apiCall.api_get_customer_by_id(customer_id)
  //   this.global.Getinstallmentbyid.subscribe(res => {
  //     console.log(res);
  //     this.installment_by_id = res; 
  //    });
 
  //  const modal = await this.modal.create({
  //     component: ModalPage,
  //     cssClass: 'my-modal-class',
  //     initialBreakpoint: 0.93 ,
  //     breakpoints: [0, 0.435, 0.93],
  //     componentProps: {
  //       'Category' : this.Category,
  //       'Monthly_Installment_details' : this.installment_by_id,
  //     }
  //   });
  //   console.log('modal')
  //   return await modal.present();
  // }

  async seeDeatils(data) {
    const modal = await this.modalController.create({
      component: ModalPage,
      cssClass: 'my-modal-class',
      componentProps: {
        'installmentdetails': data,
        'Category': "viewinstallmentdetails",
      }
    });
    return await modal.present();
  }

}
